<template>
  <div>
    <h1 v-if="!this.$store.state.select.isChosen">Choose section!</h1>
    <Words v-if="this.$store.state.select.isChosen"></Words>
    <div class="buttons" v-else>
      <button
        type="button"
        class="btn btn-warning"
        v-for="(section, index) in sections"
        @click="onChoose(index)">
        {{ section }} ({{ arrlength[index] }})
      </button>
    </div>
  </div>
</template>

<script>
  import Words from "./components/Words.vue";
  export default {
    data: function () {
      return {
        sections: [],
        arrlength: []
      }
    },
    created() {
      this.$store.state.Words.map(item => {
        this.sections.push(item.section);
        this.arrlength.push(item.words.length)
      })
    },
    methods: {
      onChoose(index) {
        this.$store.state.select.isChosen = true;
        this.$store.state.select.arrIndex = index;
      }
    },
    components: {
      Words
    }
  }
</script>

<style>
  body {
    font-family: 'Roboto', sans-serif;
    background: lightgreen;
  }
  h1 {
    margin: 20px;
    text-align: center;
  }
  .buttons{
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>
