<template>
  <main>
    <div class="main-btns">
      <button
        type="button"
        class="btn btn-danger"
        @click="back()">
        Back
      </button>
      <button
        type="button"
        class="btn btn-success"
        @click="test()">
        Test
      </button>
    </div>
    <h3>{{ shownWord.eng }}</h3>
    <div class="list">
      <ul>
        <li v-for="(option, i) in finalArray"
            @click="check(option)">
          <button
            type="button"
            class="btn btn-warning">
            {{ option.ukr }}
          </button>
        </li>
      </ul>
      <span>Correct / Incorrect answers - {{ correctAnswers }} / {{ incorrectAnswers }}</span>
    </div>
  </main>
</template>

<script>
  export default {
    data: function () {
      return {
        shownWord: '',
        options: [],
        finalArray: [],
        correctAnswers: 0,
        incorrectAnswers: 0
      }
    },
    methods: {
      back() {
        this.$store.state.select.isChosen = false;
      },
      test(a = Math.round(Math.random() * this.$store.state.Words[this.$store.state.select.arrIndex].words.length),
           b = Math.round(Math.random() * this.$store.state.Words[this.$store.state.select.arrIndex].words.length)) {
        this.shownWord = this.$store.state.Words[this.$store.state.select.arrIndex].words[a];
        for (let option of this.$store.state.Words[this.$store.state.select.arrIndex].words) {
          if (option[b] !== this.shownWord) {
            this.options.push(option);
          }
        }
        function random() {
          return Math.random() - 0.5;
        }
        if (!(this.finalArray.includes(this.shownWord))) {
          this.finalArray = this.options.slice(0,3);
          this.finalArray.unshift(this.shownWord);
          console.log(this.finalArray);
        } else {
          this.finalArray = this.options.slice(0,4);
        }
        this.finalArray.sort(random);
      },
      check(option) {
        if (option === this.shownWord) {
          alert('Correct');
          this.correctAnswers++;
        } else {
          alert('Wrong');
          this.incorrectAnswers++;
        }
        this.test();
        this.options = [];
      }
    },
  }
</script>

