  main {
    text-align: center;
  }
  .main-btns button{
    margin-top: 40px;
  }
  h3{
    margin: 20px;
  }
  ul {
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: center;
    li{
      margin: 0 20px;
    }
  }
  button {
    margin: 0 10px;
  }
</style>
